<template>
    <div >
      <div v-if="DataBadge.length > 0 && BadgeStyle == 'badge' " class="m- user-select-none badgeContainer">
        <!-- <small v-for="badge in DataBadge" :key="index"  @click="unsel(index)" class="badge bg-dark">{{badge.text}}</small> -->
        <span  v-for="badge in DataBadge" :key="badge.value" @click="unsel(badge.value)" class="m-1 pointer ">
        <small  class="badge bg-dark">{{badge.text}}  x</small>
        </span>
      </div>

      <div v-if="DataBadge.length > 0 && BadgeStyle == 'list' " class=" user-select-none badgeContainer">
        <ul class="p-0 w-100 border ">
          <li v-for="badge in DataBadge" :key="badge.value"  class="list-unstyled dataList p-1 ps-2 pe-2 rounded m-1 d-flex justify-content-between ">
            {{badge.text}} 
            <img src="../assets/icones/cancel2.png" alt="" class="" style="width:20px" @click="unsel(badge.value)" >
          </li>
        </ul>
        
      </div>


      <label v-if="label"  for="searchInput" class="ms-2  small">{{ label }}</label>
      <input type="text" id="searchInput" :placeholder="placeholder" class="form-control" v-model="search" @keydown.delete="verifySelect" @click="showSelect = !showSelect" @focus="filterOptions" @input="filterOptions" />
      <div v-show="showSelect" class="container-fluide selectContainer">
        <select multiple v-show="showSelect" name="selectInput" id="" v-model="selectInput" class="form-control selectInput" >
        <option  v-for="option in filteredOptions" :value="option.value" 
         @click="sel()" class="pointer">{{ option.text }}</option>
      </select>

      </div>
    
      

    </div>
 
  </template>
  
  <script>
  export default {
    props: {
    Multiple:{ default:false
    },
    Label: { default: "",
    },

    Placeholder: { default: "",
    },

     BadgeStyle:{ default: "",
    },

    Options: { default: [],
    },

    dataMulti: { default: [],
    },

    data: { default: '',
    },
},
    data() {
      return {
        multiple:this.Multiple,
        label:this.Label,
        placeholder : this.Placeholder,
        search: '',
        filteredOptions: this.options,
        showSelect: false,
        inputShowSelect:false,
        selectInput:[],
        dataMulti:[],
        data:'',
        DataBadge:[],
        options:this.Options,
        badgeStyle : this.BadgeStyle
        
        
      };
    },
    computed: {
         filterOptions() {
    this.filteredOptions = this.options.filter(option => option.text.toLowerCase().includes(this.search.toLowerCase()))
    if (!this.showSelect && this.search.length > 0){
      this.showSelect = true
    }
  },
  clear(){
    if (this.search.length < 1 && !this.Multiple){
      console.log('cleaaaar')
      this.data = ''
    }
  }
 
     
    },

    methods:{
    //   showSel(){
    //     if (!this.showSelect && this.search.length ){
    //   this.showSelect = true
    // }else if(this.showSelect ){
    //   this.showSelect = false
      
    // }
    //   },
      sel(){
    if (this.multiple){

      if(!this.dataMulti.includes(this.selectInput[0])){
       
       
        // this.dataMulti.forEach(pay=>{console.log(pay)})
        
        this.dataMulti.push(this.selectInput[0])
        // console.log(this.dataMulti)
       

        this.options.forEach(opt => {
          if (opt.value === this.selectInput[0]) {
              this.DataBadge.push(opt);
              this.options = this.options.filter(element => element !== opt)
              this.filteredOptions = this.filteredOptions.filter(element => element !== opt)

            
              
            }
          });
       
      }else{
       
        // this.dataMulti.forEach(pay=>{console.log(pay)})

      this.dataMulti = this.dataMulti.filter(element => element !== this.selectInput[0])
      
      this.options.forEach(opt => {
          if (opt.value === this.selectInput[0]) {
            this.DataBadge = this.DataBadge.filter(element => element !== opt)
            this.options.push(opt)
            this.filteredOptions.push(opt)
            
            
            }
          });
      
      }
    }else{
      // if (this.dataMulti.length <= 0) {
       this.data=this.selectInput[0]
      //  console.log(this.data)
        this.options.forEach(opt =>{
          if (opt.value == this.selectInput[0]){this.search = opt.text }
          this.showSelect = false
        })
      
        // console.log (this.dataMulti)
      // }
      // else{
      //       this.dataMulti= []
      //       this.dataMulti.push(this.selectInput[0])
      //       this.options.forEach(opt =>{
      //         if (opt.value == this.selectInput[0]){this.search = opt.text }
      //         this.showSelect = false
      //       })
      //         // console.log (this.dataMulti)

      //     }
      
    }
      

 
  },
  unsel(value){
    if (this.multiple){


        this.showSelect = false
        
        let index= this.DataBadge.findIndex(badge => badge.value === value);
        this.dataMulti = this.dataMulti.filter(element => element !== value)
        this.options.push(this.DataBadge[index])
        this.filteredOptions.push(this.DataBadge[index])
        this.DataBadge = this.DataBadge.filter(element => element !== this.DataBadge[index])
        // console.log(this.dataMulti)

    }
  },

  // FONCTION UNIQUEMENT SUR SELECT UNIQUE  
  verifySelect(){
    this.options.forEach(opt =>{
              if (this.search.length < 1 && !this.Multiple){
                console.log('cleeeeeee')
                this.data = '' }
            })
  },

   

    },

watch: {
  dataMulti: {
    handler() {
      this.$emit("update:dataMulti", this.dataMulti);
      // this.$emit("update:data", this.data);
      
    },
    deep: true,
  },
  data: {
    handler() {
      // this.$emit("update:dataMulti", this.dataMulti);
      this.$emit("update:data", this.data);
      
    },
    deep: true,
  },
},
  };
  </script>
  
  <style>
.pointer{
    cursor: pointer;
}
.selectContainer{
  position:relative;
}
.badgeContainer{
  overflow: scroll;
  max-height: 120px;
}
.selectInput{
  position: absolute;
  top: 100%;
  z-index: 100;
  
}
.dataList{
  -webkit-box-shadow: inset 0px 0px 13px 0px rgb(0, 0, 0); 
box-shadow: inset 0px 0px 13px 0px rgb(0, 0, 0);
}
</style>