<script>
import FullCalendar from '@fullcalendar/vue3'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'

import selectSearch from '@/components/selectSearche.vue'

export default {
  components:{FullCalendar,selectSearch},
  data() {
    return {
      calendarOptions: {
        plugins: [ dayGridPlugin, interactionPlugin ],
        dateClick: this.handleDateClick,
        initialView: 'dayGridMonth',
        fixedWeekCount: false,
        // dayCellContent: this.customDayCellContent,
      },
      date : "",
      add_cons_text:"",
      add_cons_date:"",
      add_cons_service:"",
      add_cons_importance:"",

      add_taxi_date:"",
      
      taxis:[1,3,5,6,63,51]
    }
  },
  mounted(){
   
   
  },
  methods:{
  
    handleDateClick(info){
      this.date = info.dateStr
      // const selectedDate = info.dateStr;
      console.log(this.date)
      this.add_cons_date = this.date
      this.add_taxi_date = this.date
      // Changer la couleur de fond de la cellule du jour sélectionné
      info.dayEl.style.backgroundColor = 'black';
      if (this.previousSelectedDayEl) {
            this.previousSelectedDayEl.style.backgroundColor = '';
      }
      this.previousSelectedDayEl = info.dayEl;
    },
    collaps(idc,classc,id){
      console.log(`${idc} ---- ${classc}  ---  ${id}`)

      var container = document.getElementById(idc)
      var content = document.getElementById(id)
      var arrow = document.getElementById(`${idc}arrow`)
      
      
      
      container.classList.toggle("open")
      content.classList.toggle("d-none")
      arrow.classList.toggle("rotate-arrow")
      
      const all_containers = document.getElementsByClassName(classc)
      // console.log(`${container} ---- ${all_containers}  ---  ${content}`)
      // console.log(all_containers)
      for(let i = 0 ; i < all_containers.length; i++ ){
        const id = all_containers[i].id
        console.log(all_containers[i])

        if (id != idc){
          container = document.getElementById(id)
          // Sélectionnez le premier élément <div> enfant
            content = container.querySelector('div');
            arrow = document.getElementById(`${id}arrow`)
            
            console.log(arrow)
            if(container.classList.contains('open')){
              container.classList.remove("open")
            }
            if(!container.classList.contains("d-none")){
          content.classList.add("d-none")
          }
         
          arrow.classList.remove("rotate-arrow")
      

        }
      }

      
    },
    show_add_object(showBtn,Form,objectsContainer,show = true){
      console.log([showBtn,Form,objectsContainer])
      // const show_btn = document.getElementById("show-add-taxi")
      // const form = document.getElementById("add-taxi")
      // const taxis_container = document.getElementById("display-taxis")
      const show_btn = document.getElementById(showBtn)
      const form = document.getElementById(Form)
      const taxis_container = document.getElementById(objectsContainer)
      console.log([show_btn,form,taxis_container])



      show_btn.classList.toggle('d-none')
      form.classList.remove('d-none')

      var hauteur = taxis_container.offsetHeight
      var form_hauteur = form.offsetHeight
      
      // var btn_hauteur = show_btn.offsetHeight
      var new_hauteur = (hauteur - form_hauteur) + 35
      
      if (show == false){
        hauteur = taxis_container.offsetHeight
        new_hauteur = (hauteur + form_hauteur) - 35
        form.classList.add('d-none')
      }

      taxis_container.style.height=`${new_hauteur}px`
      taxis_container.style.minHeight=`${new_hauteur}px`

    },
    add_object_validation(object,mode){

      if (mode == 'validate'){
        console.log("validation")
      }

      if (object == "taxi"){
      this.show_add_object('show-add-taxi','add-taxi','display-taxis',false)
      }else if (object == "reveille"){
        this.show_add_object('show-add-reveille','add-reveille','display-reveilles',false)
      }else if (object == "categ"){
        console.log('categggggg')
        this.show_add_object('show-add-contact','add-categ','display-contact',false)
      }else if (object == "contact"){
        this.show_add_object('show-add-contact','add-contact','display-contact',false)
        console.log('contaaaa')

      }

      }

   
      
  }
}

</script>

