          password: this.password
          // password: passwordHash
          
        })
        // AJOUTER COOKIE AU NAVIGATEUR
        console.log(response.data)
        Cookies.set('token', response.data.jwt)


        
        this.$store.dispatch('checkUser')
      
     

      
