<script>
import spinBox from '@/components/patform-comp/Spinbox.vue'
import selectSearch from '@/components/selectSearche.vue'

export default {
  components:{spinBox,selectSearch},
  data(){
    return {
      caisse_actuelle :{

        monnaie:{
          500:[0,0],
          200:[0,0],
          100:[0,0],
          50:[0,0],
          20:[0,0],
          10:[0,0],
          5:[0,0],
          2:[0,0],
          1:[0,0],
          0.5:[0,0],
          0.2:[0,0],
          0.1:[0,0],
          0.05:[0,0],
          0.02:[0,0],
          0.01:[0,0],
        },
        total_caisse:0,
        total_ecart :0,
        total_depense:0,
        liste_depense:[],
        total_cashIn:0,
        in_caisse_radio: false,
        in_coffre_radio: false,
        commentaire:"",
        emeteur:"",
        receveur:"",
      },

      dep_montant_input:"",
      dep_libele_input:"",
      total_cashIn_input:"",
      inputValue:'',

      caisse_précédente:{
        monnaie:{
          500:[0,0],
          200:[0,0],
          100:[0,0],
          50:[0,0],
          20:[0,0],
          10:[0,0],
          5:[0,0],
          2:[0,0],
          1:[0,0],
          0.5:[0,0],
          0.2:[0,0],
          0.1:[0,0],
          0.05:[0,0],
          0.02:[0,0],
          0.01:[0,0],
        },
        total_caisse:300,
        total_ecart :0,
        total_depense:0,
        liste_depense:[],
        total_cashIn:0,
        in_caisse_radio: false,
        in_coffre_radio: false,
      },
      // total_depense:"éé"

      errors:{
        justif_ecart:"",
        caisse_vide:"",
        ajout_receveur:"",
        ajout_cashIn:"",
      }
      


      


    }
  },
  created(){
      this.calc_total()
  },
  methods:{

    calc_total(){

      // calcule total monnaie
      this.caisse_actuelle['total_caisse']  = 0
    
      const monnaie_cles = Object.keys(this.caisse_actuelle['monnaie'])
      monnaie_cles.forEach(cle => {
        this.caisse_actuelle['total_caisse'] += parseFloat(this.caisse_actuelle['monnaie'][cle][1])
      });
      // convertire en chiffre 0.00 
      this.caisse_actuelle['total_caisse']  = this.caisse_actuelle['total_caisse'] .toFixed(2)
      this.calc_ecart()
    },

    calc_ecart(){
    
      this.caisse_actuelle['total_ecart'] = 0
      this.caisse_actuelle['total_ecart'] = parseFloat(this.caisse_actuelle['total_caisse']) - parseFloat(this.caisse_précédente["total_caisse"]) 

      if (this.caisse_actuelle['total_depense'] > 0){
        this.caisse_actuelle['total_ecart'] += parseFloat(this.caisse_actuelle['total_depense'])
      }

      if (parseFloat(this.caisse_actuelle['total_cashIn']) > 0 & this.caisse_actuelle["in_caisse_radio"]){
        this.caisse_actuelle['total_ecart'] -= parseFloat(this.caisse_actuelle['total_cashIn'])
      }

      this.caisse_actuelle['total_ecart'] = parseFloat(this.caisse_actuelle['total_ecart']).toFixed(2)
      
    },

    calc_somme(somme_info){
      console.log(somme_info)
      const nombre = parseInt(somme_info[1])
      const type = parseFloat(somme_info[0])
      var somme = parseFloat(nombre) * parseFloat(type)
      somme = somme.toFixed(2)
      // console.log(this.caisse_actuelle['monnaie'][type])

      this.caisse_actuelle['monnaie'][type][1] = somme
      this.caisse_actuelle['monnaie'][type][0] = nombre

      this.calc_total()
     
    },

    calc_depenses(){
      
      this.caisse_actuelle['total_depense'] = 0
      this.caisse_actuelle['liste_depense'].forEach(depense => {
        this.caisse_actuelle['total_depense'] += parseFloat(depense[0])
      })
      
      this.caisse_actuelle['total_depense'] = parseFloat(this.caisse_actuelle['total_depense']).toFixed(2)
      
      this.calc_total()
    },

    add_cashOut(){
      const libele = this.dep_libele_input
      const montant = this.dep_montant_input
      const depense = [montant,libele]

      this.caisse_actuelle['liste_depense'].push(depense)

      this.dep_montant_input = ""
      this.dep_libele_input = ""
      
      this.calc_depenses()
    },
    
    delete_cashOut(dep){
      
      this.caisse_actuelle['liste_depense'].forEach(depense => {
        if (depense == dep){
          this.caisse_actuelle['liste_depense'].pop(depense) 
        }
      })
      


      
      this.calc_depenses()
    },
    caisse_validation(){
      var caisse_valide = true
      this.errors = []
      const total_caisse = this.caisse_actuelle['total_caisse']
      const total_cashIn = this.caisse_actuelle['total_cashIn']
      const in_caisse = this.caisse_actuelle['in_caisse_radio']
      const in_coffre = this.caisse_actuelle['in_coffre_radio']
      const receveur = this.caisse_actuelle['receveur']
      const ecart = this.caisse_actuelle['total_ecart']
      const commentaire = this.caisse_actuelle['commentaire']

      if (total_caisse == 0){
        var error = "impossible d'ajouter une caisse null"
        this.errors['caisse_vide'] = error
        caisse_valide = false
      }
      if (total_cashIn > 0 && in_caisse == false && in_coffre == false  ){
        var error = " Encaissement Caisse / Coffre ? "
        this.errors['ajout_cashIn'] = error
        caisse_valide = false
      }
      if (ecart != 0 && commentaire.length == 0 ){
        var error = "vous devez justifier l'ecart"
        this.errors['justif_ecart'] = error
        caisse_valide = false
      }
      if (receveur.length == 0){
        var error = "vous devez selectioner un receveur"
        this.errors['ajout_receveur'] = error
        // caisse_valide = false
      }

      return caisse_valide
    
    },
    show_signature_dialog(){
      const valide = this.caisse_validation()
      const section= document.getElementById("section")
      const validation_window = document.getElementById("dialog-window")
      if (valide){
        validation_window.classList.toggle('d-none')
        section.style.filter="brightness(0.15)"
        console.log(validation_window)
      }else{
        console.log("not valide")
      }

    },
    cancel_signature_dialog(){
      const section= document.getElementById("section")
      const validation_window = document.getElementById("dialog-window")
      
      validation_window.classList.add('d-none')
      section.style.filter="none"

    },


    
  }
}

</script>

