<template>
  <div class="container-fluide ">
  <div class="FormContainer m-auto container-fluide mt-4 text-center p-2 rounded mt-5">
      <h4 class="mt-3">Sign up</h4>
        <form @submit.prevent="register" class="text-start m-auto pb-3">
        
          <div class="form-group">
            <label for="firstName" class="ms-2 small">First Name</label>
            <input type="text" class="form-control" id="firstName" v-model="firstName" />
            <div v-if="firstNameErrors.length > 0">
              <ul>
                <li v-for="Error in firstNameErrors" class="FieldErrors"> {{ Error }}</li>
              </ul>
            </div>

          </div>

          <div class="form-group">
            <label for="lastName" class="ms-2 small">Last Name</label>
            <input type="text" class="form-control" id="lastName" v-model="lastName"  />
            <div v-if="lastNameErrors.length > 0">
              <ul>
                <li v-for="Error in lastNameErrors" class="FieldErrors"> {{ Error }}</li>
              </ul>
            </div>
          </div>

          <div class="form-group">
            <label for="email" class="ms-2 small">Email:</label>
            <input type="email" class="form-control" id="email" v-model="email" />
            <div v-if="emailErrors.length > 0">
              <ul>
                <li v-for="Error in emailErrors" class="FieldErrors"> {{ Error }}</li>
              </ul>
            </div>
          </div>

          <div class="form-group">
            <label for="password1" class="ms-2 small">Password:</label>
            <input type="password" class="form-control" id="password1" v-model="password1" />
            <div v-if="passwordErrors.length > 0">
              <ul>
                <li v-for="Error in passwordErrors" class="FieldErrors"> {{ Error }}</li>
              </ul>
            </div>
          </div>

          <div class="form-group">
            <label for="password2" class="ms-2 small"> Confirme Password:</label>
            <input type="password" class="form-control" id="password2" v-model="password2" />
          </div>

          <div class="form-group mt-3 mb-3 text-center">
            <button type="submit" class="btn btn-outline-primary" >Register</button>
          </div>
          <div class="small">you already have an account ?  <a href="#/login">Log in</a> </div>

          <div class=" googleLogin border m-auto mt-2 d-flex align-items-center rounded">
            <img src="../assets/icones/google.png" alt="" class="googleIcone bg-white p-1">
            <div class="googleText text-center  w-100 ">Sign up with google</div>
            <div class="hov"></div>
          </div>

        </form>
      </div>
    
  </div>
    
</template>

<script>
import { mapActions,mapGetters } from 'vuex'
import axios from 'axios'
import CryptoJS from 'crypto-js'

export default {
  data() {
    return {
      firstName: '',
      lastName: '',
      email: '',
      password1: '',
      password2: '',
      formErrors: {},     /* toute les erreur du form */
      lastNameErrors:[],  /* erreurs a afficher sous le input */
      emailErrors:[],     /* erreurs a afficher sous le input */
      firstNameErrors:[], /* erreurs a afficher sous le input */ 
      passwordErrors:[],  /* erreurs a afficher sous le input */
    }
  },
  computed:{
    // ...mapGetters('formValidation', ['formIsValide'])
  },

  methods: {
    // importer formvalidation depuis store 
    ...mapActions('formValidation', ['formValidation']),

    async register() {
      this.firstNameErrors = []
      this.lastNameErrors = []
      this.emailErrors = []
      this.passwordErrors = []

      const fieldsArray = [
          {'field':this.firstName,'name':'nom',  'errArray':this.firstNameErrors},
          {'field':this.lastName,'name':'prenom','errArray':this.lastNameErrors},
          {'field':this.email,'name':'email','errArray':this.emailErrors},
          {'field':this.password1,'name':'mot de passe','errArray':this.passwordErrors},

   
         
        ]

      let formIsValide = await this.formValidation(fieldsArray)
      
      // verifier si le password et la confirmation du password sont identique 
      if(this.password1 !== this.password2){
        formIsValide = false
        console.log("false from password")
        let error = 'le mot de passe 1 et mot de passe 2 ne sont pas identique.'
        this.passwordErrors.push(error)
      }
  

        // si le fomulaire est valide  
        if (formIsValide) {
            console.log('form is valid')

            // crypter le mot de pass 
            const passwordHash = CryptoJS.SHA256(this.password1).toString()
            
            // envoyer requette au serveur pour créer un nouvel utilisateur 
          try {  const response = await  axios.post('http://127.0.0.1:8000/signin', {
            firstName: this.firstName.toString(),
            lastName: this.lastName.toString(),
            email: this.email.toString(),
            password1: passwordHash,    
            })
          
              console.log('succes!!!!') 
              console.log(response.data)

          }
          catch (err) {
        console.error(err)
          }

        
        }else{
          console.log('form is noooot valid')

        }


      }
    
    },
    
  }






</script>

