<template>
  <div class="d-flex" style="height:30px;width:60px">
    <input class="text-center" type="number" min="0" :id="P_id" value="0">
    <div class="d-blok " style="height:25px; width:30px;padding:0px;border:none;">
      <!-- <div class="button" id="increment" >+</div>
      <div class="button mt-1" id="decrement" >-</div> -->
      <div @click="this.increment()" class="btn-container" style="height:15px;width: 30px;">
        <img src="../../assets/icones/fleche2.png" class="button" id="increment" />
      </div>
      <div @click="this.decrement()" class="btn-container " style="height:15px;width: 30px;">
        <img src="../../assets/icones/fleche.png" class="button"  id="decrement"/>
      </div>
    </div>
  </div>
</template>style="height:13px ;"

<script>
export default {
  props:["P_id"],
  data(){
    return{
      id:''
    }
  },
  created(){
   
    this.id = this.P_id
    // const spinbox = document.getElementById("spinbox");
    // const incrementButton = document.getElementById("increment");
    // const decrementButton = document.getElementById("decrement");

 

 
  },
  methods:{
    increment(){
    const spinbox = document.getElementById(this.P_id);
    spinbox.value = parseInt(spinbox.value) + 1;
    this.$emit('input-changed',[spinbox.value,this.P_id]);

    },
    decrement(){
    const spinbox = document.getElementById(this.P_id);
    spinbox.value = parseInt(spinbox.value) - 1;
    this.$emit('input-changed',[spinbox.value,this.P_id]);

    }
  }
  
 
}
</script>

