import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView.vue'
import LoginView from '../views/Login.vue'
import SignupView from '../views/Signup.vue'
import SubHomeView from '../views/patform-views/SubHome.vue'
import DashView from '../views/patform-views/Dashboard.vue'
import CaisseView from '../views/patform-views/Caisse.vue'
import PreferancesView from '../views/patform-views/Preferances.vue'
import SubLoginView from '../views/patform-views/SubLogin.vue'



var pages = [
  ['',"home",HomeView],
  ['about',"About",AboutView],
  ['login',"Login",LoginView],
  ['signup',"Signup",SignupView],
  ['subhome',"SubHome",SubHomeView],
  ['caisse',"Caisse",CaisseView],
  ['sublogin',"Sublogin",SubLoginView],
  ['dash',"Dash",DashView],
  ['preferances',"Preferances",PreferancesView],

]

var routes = []

pages.forEach(page => {
  const obj = {
    path: `/${page[0]}`,
    name: page[1],
    component: page[2]
  }

  routes.push(obj)
  
});



const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
