<template>
  
  <sideBar @collaps-changed="onCollapsChanged"/>
  <section class="ps-2 pe-2" id="router" >
    
    <router-view/>

  </section>
</template>
<script>
import sideBar from '@/components/side-bar.vue'
export default {
  components:{sideBar},
  data(){
    return{
      collaps:true,
    }
  },
  created() {
    // Ajoutez un écouteur d'événements pour le redimensionnement de la fenêtre
    window.addEventListener('resize', (event) => { this.onCollapsChanged(event,null)});
  },
 
  methods:{
    onCollapsChanged(collaps) {
      if (collaps != null ){
        this.collaps = collaps
      }else{
        collaps = this.collaps
      }
      console.log(`${this.collaps} --- ${collaps}`)

      const router = document.getElementById('router')
      // const largeurRouter = router.offsetWidth;
      const largeurEcran = window.innerWidth;

      var largeur = largeurEcran - 200
      
      if (collaps){
        router.style.width=`${largeur}px`
        router.style.marginLeft = '200px'
      }else{
        router.style.width=`100vw`
        router.style.marginLeft = '0px'}
     
      
    }
  }
  

}

</script>

